@import url(https://fonts.googleapis.com/css2?family=Audiowide&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.main{font-family:"Audiowide", cursive;font-size:16px;background:linear-gradient(120deg, #0bf9c1, #05cc9e);min-height:100vh;display:flex;align-items:center;justify-content:center}.main .calculator{border-radius:10px;max-width:20rem;border:black solid 5px;color:white;background-color:#171717;box-shadow:5px 5px 24px -3px rgba(0,0,0,0.64)}.main .calculator .screen{min-height:9rem;padding:1rem;display:flex;flex-direction:column;justify-content:flex-end;word-wrap:break-word;word-break:break-all;text-align:end}.main .calculator #display{font-size:2.5rem;min-height:60px}.main .calculator .expression{font-size:1.2rem}.main .calculator .error{color:#ec1313;font-size:1.5rem;text-align:start}.main .by{color:black;display:block;width:100%;text-align:center;margin-top:2rem;letter-spacing:1px}.btn{font-size:1.3rem;font-weight:600;padding:1rem;width:auto;border:solid 1px black;background-color:#363636;color:white}.btn:hover{color:white;background-color:#1c1c1c}.btn:focus{box-shadow:none}.btns{display:grid;width:100%;grid-template-columns:1fr 1fr 1fr 1fr;grid-template-areas:"ac back div mult" "sev eig nin subt" "fou fiv six addi" "one two thre equa" "zer zer dec equa"}.btns #clear{grid-area:ac;background-color:#292929}.btns #clear:hover{background-color:#0f0f0f;color:white}.btns #backspace{grid-area:back;background-color:#292929}.btns #backspace:hover{background-color:#0f0f0f;color:white}.btns #divide{grid-area:div;background-color:#292929}.btns #divide:hover{background-color:#0f0f0f;color:white}.btns #seven{grid-area:sev}.btns #eight{grid-area:eig}.btns #nine{grid-area:nin}.btns #multiply{grid-area:mult;background-color:#292929}.btns #multiply:hover{background-color:#0f0f0f;color:white}.btns #four{grid-area:fou}.btns #five{grid-area:fiv}.btns #six{grid-area:six}.btns #subtract{grid-area:subt;background-color:#292929}.btns #subtract:hover{background-color:#0f0f0f;color:white}.btns #one{grid-area:one}.btns #two{grid-area:two}.btns #thre{grid-area:thre}.btns #add{grid-area:addi;background-color:#292929}.btns #add:hover{background-color:#0f0f0f;color:white}.btns #zero{grid-area:zer}.btns #decimal{grid-area:dec}.btns #equals{grid-area:equa;background-color:#ffc309}.btns #equals:hover{color:black}

